import { Component, OnInit } from '@angular/core';
import { TmdbSerial } from "../../shared/services/TmdbSerial";
import { AuthService } from "../../shared/services/auth.service";
import { SerialService } from "../../shared/services/serial.service";
import { TmdbService } from "../../shared/services/tmdb.service";
import { ActivatedRoute } from "@angular/router";

@Component({
  selector: 'app-tracker-serial-page',
  templateUrl: './tracker-serial-page.component.html',
  styleUrls: ['./tracker-serial-page.component.scss']
})
export class TrackerSerialPageComponent implements OnInit {

  private serial: TmdbSerial;
  private isWatching: boolean;
  private isLoggedIn: boolean;

  constructor(
    private auth: AuthService,
    private serialService: SerialService,
    private tmdb: TmdbService,
    private route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.serialService.getSerialInfo(+params['id']).subscribe(serial => this.onSerialReady(serial));
    });

    this.auth.getUser$().subscribe(user => this.isLoggedIn = !!user);
  }

  getPosterUrl() {
    return `http://image.tmdb.org/t/p/w500${this.serial.poster_path}`;
  }

  watchSerial() {
    this.serialService.addToWatchList(this.serial);
  }

  archiveSerial() {
    console.log('Will be archived someday');
  }

  onSerialReady(serial: TmdbSerial) {
    this.serial = serial;
    this.serialService.isWatchingSerial(this.serial).subscribe(isWatching => this.isWatching = isWatching);
  }

  login() {
    this.auth.login();
  }

}



