import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TrackerRoutingModule } from "./tracker.routing";
import { TrackerComponent } from './tracker/tracker.component';
import { SerialsPageComponent } from './serials-page/serials-page.component';
import { SharedModule } from "../shared/shared.module";
import { SerialTilesListComponent } from './serial-tiles-list/serial-tiles-list.component';
import { SerialTileItemComponent } from './serial-tile-item/serial-tile-item.component';
import { TrackerSerialPageComponent } from './tracker-serial-page/tracker-serial-page.component';
import { MdIconModule, MdCardModule } from "@angular/material";

@NgModule({
  declarations: [
    TrackerComponent,
    SerialsPageComponent,
    SerialTilesListComponent,
    SerialTileItemComponent,
    TrackerSerialPageComponent
  ],
  imports: [
    CommonModule,
    SharedModule,
    TrackerRoutingModule,
    MdIconModule.forRoot(),
    MdCardModule.forRoot()
  ],
  exports : [],
  providers : []
})
export class TrackerModule { }



