import { Component, OnInit, Input } from '@angular/core';
import { TmdbSerial } from "../../shared/services/TmdbSerial";

@Component({
  selector: 'app-serial-tile-item',
  templateUrl: './serial-tile-item.component.html',
  styleUrls: ['./serial-tile-item.component.css']
})
export class SerialTileItemComponent implements OnInit {

  @Input() serial: TmdbSerial;

  constructor() { }

  ngOnInit() {
  }

  getImageSrc(path: string) {
    return `http://image.tmdb.org/t/p/w300${path}`;
  }

}



